.card-item{
	max-width: 300px;
	text-align: center;
	border: solid 2px
	#CCCCCC ; 
	border-radius: 10px;
	margin: 1rem;
	padding: 40px 20px;
}

.Courses-pro-box h5{
	color: #F7931E;
	margin-top: 3rem;
}
.Courses-pro-box h1{
margin-top: 2rem;
margin-bottom: 3rem;
}
.Courses-pro{
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	margin-bottom: 3rem;
	
}
.Courses-pro Button{
	
	background-color: #F7931E !important;
	border: #F7931E !important;

}
@media screen and   (max-width: 1100px){
	
	.card-item{
		max-width: 230px;
		text-align: center;
		border: solid 2px
		#CCCCCC ; 
		border-radius: 10px;
		margin: 10px;
		padding: 20px 20px;
		font-size: 16px;
		& h5{
			font-size: 12px;
		}
		& p{
			height: 100px;
		}
		& img{
			width: 200px;
			margin-bottom: 0px !important;
		}
	}
	.Courses-pro Button{
		width: 130px ;
		font-size: 14px;
	}

}
@media screen and   (max-width: 868px){
	
	.card-item{
		max-width: 200px;
		text-align: center;
		border: solid 2px
		#CCCCCC ; 
		border-radius: 10px;
		margin: 5px;
		padding: 15px 15px;
		font-size: 11px;
		& h5{
			font-size: 12px;
		}
		& p{
			height: 80px;
		}
		& img{
			width: 170px;
			margin-bottom: 0px !important;
		}
	}
	.Courses-pro Button{
		width: 130px ;
		font-size: 14px;
	}

}

@media screen and   (max-width: 468px){
	
	.card-item{
		max-width: 160px;
		text-align: center;
		border: solid 2px
		#CCCCCC ; 
		border-radius: 10px;
		margin: 5px;
		padding: 11px 12px;
		font-size: 11px;
		& h5{
			font-size: 10px;
		}
		& p{
			height: 80px;
		}
		& img{
			width: 150px;
			margin-bottom: 0px !important;
		}
	}
	.Courses-pro Button{
		width: 100px ;
		font-size: 10px;
	}

}