@import"../../style/colors.scss";

.header-pro{
	margin-top: 30px;
}
.head-logo{
	margin-right: 20%;
 width:  250px;
 display: none;
}

.link-colors{
	color: #F7931E !important;
	
}
.login{
	margin-left: 80px !important;
}
.Donate{
	border:  solid #F7931E;
	border-radius: 5px;
}
.Donate:hover{
	background-color: $main-color;
	color: #ffffff !important;
	border-radius: 5px !important;
}
.logOut{
	background-color: $main-color;
	color: #ffffff !important;
	border-radius: 5px !important;

}
.header-pro button{
	color:#F7931E !important ;
	
}


.nav-btn{
	text-decoration: none !important;
	color: #F7931E;
	margin: 2px;
	
	& a{
		text-decoration: none !important;
		color: #F7931E;
	}
}
.nav-btn:hover{
	border:  solid #F7931E;
	color: #F7931E;
	border-radius: 7%;
	
}
@media screen and   (max-width: 1140px){
	
	.header-pro{
		display: flex;
		justify-content: space-between;
	}
	.head-logo{
		
		display: block;
	   }
	   .login{
		margin-left: 0px !important;
	}
}

@media screen and   (max-width: 990px){
	
	.logo-burger{
		display: flex;
		flex-direction: row-reverse;
	}
	.logo{
		width: 90%
	}
	
}
@media screen and   (max-width: 768px){
	
	.logo{
		width: 100%
	}
	.head-logo{
		margin-right: 10%;
	
	}

}